import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "./../../layouts/common/BannerTitle/BannerTitle";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "../../core/PageComponent";
import FormContato from "./../../forms/common/FormContato";
const style = {
  h2: {
    textAlign: "center",
    color: "#313131",
    fontSize: "1.875rem",
    fontWeight: "800",
    textTransform: "uppercase",
    fontFamily: "Raleway, sans-serif"
  },

  subTitle: {
    textAlign: "center",
    fontSize: "1.125rem",
    fontWeight: "500"
  }
};

export default class Contact extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside marketing-conteudo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Contato</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Contato</span>
                  </li>
                </Breadcrumb>

                <div className="section">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 text-center">
                        <p style={style.h2}>Utilize esse formulário para:</p>
                        <ul>
                          <li style={style.subTitle}>Solicitar Orçamentos</li>
                          <li style={style.subTitle}>Enviar CV</li>
                          <li style={style.subTitle}>Tirar Dúvidas</li>
                          <li style={style.subTitle}>Quaisquer assuntos relacionados à DigitoZero</li>
                        </ul>
                        <br className="clear" />
                        <div className="row">
                          <FormContato />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



