import React, { Fragment } from "react";
import Footer from "@Components/layouts/common/Footer/Footer";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "./styles.css";
import DesignSVG from "./Design/DesignSVG";
import DevelopmentSVG from "./Development/DevelopmentSVG";
import InboundSVG from "./Inbound/InboundSVG";
import AdsSVG from "./Ads/AdsSVG";
import "./styles.scss";

export default class Services extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside ads">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <div id="banner-title">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <h1>
                          <strong>Serviços</strong>
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Serviços</span>
                  </li>
                </Breadcrumb>

                <div id="services-menu" style={style.background}>
                  <div
                    className="container"
                    style={{ display: "flex", paddingTop: "2rem" }}
                  >
                    <div className="services">
                      <h3 className="title">Design</h3>
                      <DesignSVG style={{ width: 200, height: 200 }} />
                      <Link to="/design/branding">Branding</Link>
                      <Link to="/design/ux">UX</Link>
                    </div>
                    <div className="services">
                      <h3 className="title">Dev</h3>
                      <DevelopmentSVG style={{ width: 200, height: 200 }} />
                      <Link to="/development/site">Site</Link>
                      <Link to="/development/e-commerce">Loja Virtual</Link>
                      <Link to="/development/app">App</Link>
                    </div>
                    <div className="services">
                      <h3 className="title">Inbound</h3>
                      <InboundSVG style={{ width: 200, height: 200 }} />
                      <Link to="/inbound-marketing/content-marketing">
                        Conteúdo
                      </Link>
                      <Link to="/inbound-marketing/email-marketing">
                        Email Mkt
                      </Link>
                      <Link to="/inbound-marketing/google-analytics">
                        Analytics
                      </Link>
                      <Link to="/inbound-marketing/corporate-blog">Blog</Link>
                    </div>
                    <div className="services">
                      <h3 className="title">Ads</h3>
                      <AdsSVG style={{ width: 200, height: 200 }} />
                      <Link to="/ads/google-adwords">Adword</Link>
                      <Link to="/ads/facebook-ads">Facebook Ads</Link>
                      <Link to="/ads/linkedin-ads">Linkedin Ads</Link>
                      <Link to="/ads/remarketing">Remarketing</Link>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



