import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import UXSVG from "./UXSVG";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";

export default class UX extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside usabilidade">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>UX</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>UX</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <UXSVG />
                    <h2 style={style.mainTitle}>A experiência do usuário</h2>
                    <p style={style.subTitle}>Conjunto de aspectos referentes à interação do usuário</p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Experiência de Usuário</h3>
                        <p style={style.p}>
                          A dígitozero conta com um time de desenvolvedores experiente e treinado nas principais tecnologias que o mercado oferece. Somos especialistas em desenvolvimento web, criamos sites, aplicativos e lojas online em diversas linguagens e para diversas plataformas.
                        </p>
                        <h3 style={style.h3}>O Google e a Valorização da UX</h3>
                        <p style={style.p}>
                          Você já entrou em um site disposto a comprar algo e
                          desistiu após perceber que as páginas eram mal
                          pensadas, difíceis de entender e com design ruim?
                          Alguma das 3 opções? Todas juntas? Pois é, a grande
                          maioria das pessoas tende a desistir rapidamente se
                          percebem que um site não é bem como elas esperavam.
                          Estamos nos acostumando cada vez mais com agilidade e
                          facilidade, e cada vez menos temos paciência para
                          sites que não funcionam bem. Daí toda a importância
                          dessa ciência moderna, chamada de User Experience.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          UX está relacionada ao design, que é responsável por
                          desenvolver a interface de forma que favoreça a
                          usabilidade e máxima facilidade de entendimento do
                          usuário enquanto navega. Contudo, UX não se resume
                          apenas a isso. Trata-se de uma ciência dinâmica e
                          bastante subjetiva, que depende da percepção
                          individual do usuário e tende a mudar de tempos em
                          tempos, devido à constantes inovações tecnológicas e
                          de padrões de uso da internet.
                        </p>
                        <p style={style.p}>
                          O algoritmo do Google está sempre evoluindo e já
                          consegue reconhecer muitos padrões de UX, valorizando
                          quem os segue e penalizando aqueles que não investem
                          em usabilidade. O resultado disso impacta diretamente
                          no SEO.
                        </p>
                        <p style={style.p}>
                          A dígitozero conta com um time de design com bastante
                          experiência em projetos de usabilidade e antenado nas
                          melhores práticas adotadas pelo mercado atualmente.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.UX} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



