import React, { Component } from "react";
import axios from "axios";
import { API_HOST } from './../../../environment';

class FormContato extends Component {

  reset = () => {
    this.setState({
      name:"",
      lastName:"",
      mailContact:"",
      site:"",
      company:"",
      message:""
    })

  }

  onSubmit = event => {
    event.preventDefault()
    const serviceName = this.props.serviceName || ""
    const data = { ...this.state, services: {[serviceName]: true } }

    axios.post(API_HOST + "/contato", data)
    .then( () => {
      this.reset()
      alert("Dados enviados com sucesso, em breve entraremos em contato!")
    })
    .catch( () => alert("Falha ao enviar o formulário, verifique a conexão e tente novamente"))
  }

  render() {
    return (
      <form className="form-default" onSubmit={this.onSubmit}>
        <div className="container">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-nome" className="lbl-title">
                      Seu nome?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="name"
                      className="form-control border"
                      id="txt-nome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-sobrenome" className="lbl-title">
                      Seu Sobrenome?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="lastName"
                      className="form-control border"
                      id="txt-sobrenome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-email" className="lbl-title">
                      Seu e-mail?*
                    </label>
                    <input onChange={event => this.setState({mailContact: event.target.value.toUpperCase()})}
                      type="email"
                      name="mailContact"
                      className="form-control border"
                      id="txt-email"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-phone" className="lbl-title">
                      Telefone?
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="phone"
                      className="form-control border"
                      id="txt-phone"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-empresa" className="lbl-title">
                      Nome da empresa?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="company"
                      className="form-control border"
                      id="txt-empresa"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-subject" className="lbl-title">
                      Assunto?
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="subject"
                      className="form-control border"
                      id="txt-subject"
                      required
                    />
                  </div>
                </div>

                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content">
                    <label htmlFor="txt-necessidade" className="lbl-title">
                      Nos diga sua necessidade
                    </label>
                    <textarea onChange={this.handleChange}
                      name="message"
                      id="txt-necessidade" className="form-control" />
                  </div>
                </div>
                <div className="wrapper-content">
                  <button className="button btn-send">Enviar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }

  handleChange = (e) => {
    this.setState({ [e.type] : e.target.value })
  }

}

export default FormContato;



