import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import GoogleAnalyticsSVG from "./GoogleAnalyticsSVG";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import servicesName from "../services.constant";

export default class GoogleAnalytics extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside google-analytics">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Google Analytics</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/inbound">Inbound</Link>
                  </li>
                  <li>
                    <span>Google Analytics</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <GoogleAnalyticsSVG />
                    <h2 style={style.mainTitle}>Monitore e analise</h2>
                    <p style={style.subTitle}>
                      Descubra dados e informações relevantes
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é E-mail Marketing?</h3>
                        <p style={style.p}>
                          O Google Analytics é uma ferramenta de monitoramento e
                          análise do seu{" "}
                          <HotLink to="/development/site">site</HotLink>,{" "}
                          <HotLink to="/development/app">app</HotLink> ou{" "}
                          <HotLink to="/inbound-marketing/corporate-blog">
                            blog
                          </HotLink>, na qual é possível descobrir dados e
                          informações relevantes quanto a visitas, cliques,
                          conversões e comportamento do usuário dentro do seu
                          domínio.
                        </p>

                        <p style={style.p}>
                          Analise os resultados e otimize suas ações! O Google
                          Analytics é um instrumento fundamental para mensuração
                          de desempenho do trabalho de Marketing Digital,
                          otimização de esforços e descoberta de novas
                          oportunidades de melhoria. Portanto, trata-se de uma
                          importante ferramenta de inteligência de negócio,
                          muito útil para produzir insights sobre a empresa e
                          conduzi-la às melhores decisões e caminhos a serem
                          percorridos.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          O Analytics tem uma versão gratuita bastante completa,
                          e pode ser integrado a outros serviços do Google, como{" "}
                          <HotLink to="/ads/google-adwords">
                            Google AdWords
                          </HotLink>{" "}
                          e Google Search Console, para ajudar na otimização de <HotLink to="/inbound-marketing/seo">
                            SEO
                          </HotLink>. Com o Analytics instalado da forma correta, você
                          consegue descobrir o perfil de quem mais acessa seu
                          site, as páginas mais e menos acessadas, dispositivos
                          utilizados, dados geográficos, demográficos e horários
                          e dias de pico e muito mais.
                        </p>
                        <p style={style.p}>
                          Precisa de ajuda com o Google Analytics? Solicite o
                          contato de um de nossos especialistas!
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.INBOUND} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>
            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



