import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import MarketingConteudoSVG from "./MarketingConteudoSVG";

export default class MarketingConteudo extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside marketing-conteudo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Marketing de Conteúdo</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/inbound">Inbound</Link>
                  </li>
                  <li>
                    <span>Marketing de Conteúdo</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <MarketingConteudoSVG />
                    <h2 style={style.mainTitle}>
                      Conteúdo objetivo e criativo
                    </h2>
                    <p style={style.subTitle}>
                      Eduque, oriente, informe, ajude e entreter o seu
                      público-alvo
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é Marketing de Conteúdo?</h3>
                        <p style={style.p}>
                          Marketing de conteúdo é considerado como toda peça de
                          conteúdo criada com o objetivo de educar, orientar,
                          informar, ajudar e/ou entreter o público-alvo.
                          Produzir conteúdo é uma prática importante para ganhar
                          mais visibilidade, melhorar o{" "}
                          <HotLink to="/inbound-marketing/seo">SEO</HotLink>, e
                          uma ótima estratégia para se relacionar com um atual
                          ou futuro cliente, construindo autoridade e
                          conduzindo-o em direção a compra, que é a ação final
                          desejada.
                        </p>
                        <p style={style.p}>
                          Marketing de Conteúdo é a nova forma de se fazer
                          marketing, é o cerne da estratégia de{" "}
                          <HotLink to="/inbound-marketing">
                            Inbound Marketing
                          </HotLink>{" "}
                          para qualquer empresa que busca atrair clientes
                          através de conteúdo de qualidade que seja de fato
                          relevante para esta audiência. O novo marketing
                          digital funciona nesta lógica de atrair o cliente e
                          ser facilmente encontrado online ao invés de produzir
                          propagandas e dispará-las de forma interruptiva,
                          normalmente irritando o usuário mais do que
                          propriamente sendo eficaz ao passar a mensagem
                          desejada. Seja no Facebook, Instagram, LinkedIn ou
                          blog, criar conteúdo é a melhor forma de engajar
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          seu potencial consumidor. Investir na criação de
                          conteúdo relevante que gere valor para sua audiência,
                          faz com que seu público-alvo tenha uma percepção
                          positiva sobre sua empresa, possibilitando expandir
                          sua base de leads e originar novas oportunidades de
                          negócio.
                        </p>
                        <p style={style.p}>
                          Criar conteúdo relevante é a talvez a principal
                          atividade do Marketing Digital. É fundamental ter
                          sempre na mente o que seu cliente necessita e os
                          problemas que enfrenta no dia-dia. Por isso, antes de
                          começar a criação é preciso mapear o perfil do seu
                          público-alvo, que no Marketing chamamos de Persona.
                          Este processo faz parte do Planejamento de Marketing,
                          realizado no início do trabalho de Inbound, que irá
                          basear toda a comunicação da marca. Além disso, o
                          conteúdo é extremamente importante para melhorar o
                          ranqueamento de SEO, pois realizamos estudos acerca
                          das palavras-chave mais utilizadas pela persona para
                          pesquisar no Google, e as incluímos nos temas e
                          pautas.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico
                  service={servicesName.CONTENT_MARKETING}
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



