import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./index.scss";
import "bootstrap/scss/bootstrap.scss";
import "./animate.min.css";
import "./common.sass";
// import registerServiceWorker from "./registerServiceWorker";

//Pages
import App from "./components/pages/App/App";
import Empresa from "./components/pages/Empresa";
import Orcamento from "./components/pages/Orcamento";
import NotFound from "./components/pages/NotFound";
import EmailMarketing from "./components/pages/Services/Inbound/EmailMarketing";
import GoogleAnalytics from "./components/pages/Services/Inbound/GoogleAnalytics";
import MarketingConteudo from "./components/pages/Services/Inbound/MarketingConteudo";
import SEO from "./components/pages/Services/Inbound/SEO";
import BlogCorporativo from "./components/pages/Services/Inbound/BlogCorporativo";
// import YoutubeAds from "./components/pages/Services/Ads/YoutubeAds"; 
import Remarketing from "./components/pages/Services/Ads/Remarketing";
import LinkedinAds from "./components/pages/Services/Ads/LinkedinAds";
import GoogleAdwords from "./components/pages/Services/Ads/GoogleAdwords";
import FacebookAds from "./components/pages/Services/Ads/FacebookAds";
import SponsoredLinks from "./components/pages/Services/Ads/SponsoredLinks";
import Branding from "./components/pages/Services/Design/Branding";
import UX from "./components/pages/Services/Design/UX";
import LojaVirtual from "./components/pages/Services/Development/LojaVirtual";
import Site from "./components/pages/Services/Development/Site";
import AppService  from './components/pages/Services/Development/App';
import Design from "./components/pages/Services/Design/Design";
import Inbound from './components/pages/Services/Inbound/Inbound';
import Development from './components/pages/Services/Development/Development';
import registerServiceWorker from './registerServiceWorker'

import Ads from './components/pages/Services/Ads/Ads';

// import ApolloClient from "apollo-boost";
// import { ApolloProvider } from "react-apollo";
import Contact from './components/pages/Contact/Contact';
import Services from "./components/pages/Services/Services";

// const client = new ApolloClient({
//   uri: "http://localhost:4000/graphql"
// });

ReactDOM.render(
  // <ApolloProvider client={client}>
    <Router>
      <Switch>

        <Route exact path="/services" component={Services} />
        <Route exact path="/" component={App} />
        <Route exact path="/empresa" component={Empresa} />
        <Route exact path="/price" component={Orcamento} />
        <Route exact path="/contact" component={Contact} />
        {/* <Route exact path="/blog" component={Blog} />
        <Route exact path="/ads/blog-interno" component={BlogInterno} /> */}

        {/* Ads */}
        <Route exact path="/ads" component={Ads} />
        <Route exact path="/ads/google-adwords" component={GoogleAdwords} />
        <Route exact path="/ads/sponsored-links" component={SponsoredLinks} />
        <Route exact path="/ads/facebook-ads" component={FacebookAds} />
        {/* <Route exact path="/ads/instagram-ads" component={????} /> */}
        <Route exact path="/ads/linkedin-ads" component={LinkedinAds} />
        <Route exact path="/ads/remarketing" component={Remarketing} />
        {/* <Route exact path="/ads/youtube-ads" component={YoutubeAds} /> */}

        {/* Inbound Marketing */}
        <Route exact path="/inbound-marketing" component={Inbound} />
        <Route exact path="/inbound-marketing/seo" component={SEO} />
        <Route exact path="/inbound-marketing/content-marketing" component={MarketingConteudo} />
        <Route exact path="/inbound-marketing/corporate-blog" component={BlogCorporativo} />
        <Route exact path="/inbound-marketing/email-marketing" component={EmailMarketing} />
        <Route exact path="/inbound-marketing/google-analytics" component={GoogleAnalytics} />
        {/* <Route exact path="/google-tag-manager" component={GoogleTagManager} /> */}

        {/* Design */}
        <Route exact path="/design" component={Design} />
        <Route exact path="/design/branding" component={Branding} />
        <Route exact path="/design/ux" component={UX} />
        {/* <Route exact path="/ui" component={UI} /> */}
        
        {/* Development */}
        <Route exact path="/development" component={Development} />
        <Route exact path="/development/site" component={Site} />
        <Route exact path="/development/e-commerce" component={LojaVirtual} />
        <Route exact path="/development/app" component={AppService} />

        <Route exact path="**" component={NotFound} />
      </Switch>
    </Router>
  // </ApolloProvider>
  ,
  document.getElementById("root")
);
registerServiceWorker();



