import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import DesignSVG from './DesignSVG';

export default class Design extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside design">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Design</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Design</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <DesignSVG />
                    <h2 style={style.mainTitle}>
                      Mais que uma marca: um conceito
                    </h2>
                    <p style={style.subTitle}>
                      Ganhe o consumidor com uma imagem consistente
                    </p>
                    <span style={style.hr} />
                    <p style={{ ...style.p, textAlign: "center" }}>
                      A equipe de Design da dígitozero é multidisciplinar,
                      criativa e está sempre antenada à novas tendências de
                      mercado. Realizamos projetos de <HotLink to="/design/branding">branding</HotLink> de diversos
                      tipos, seja qual for a necessidade do cliente. Crie uma <HotLink to="/design/branding">logomarca</HotLink> marcante para a sua empresa: Nós planejamos todo
                      o conceito e a <HotLink to="/design/branding">identidade visual</HotLink>, baseados em estudos do
                      público-alvo! Fazemos sites de acordo com as melhores
                      práticas de UX e garantimos a melhor <HotLink to="/design/ux">usabilidade</HotLink> possível
                      para quem navega!
                    </p>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.DESIGN} />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



