import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import servicesName from "../services.constant";
import SEOSVG from "./SEOSVG";

export default class SEO extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside seo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>SEO</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/inbound">Inbound</Link>
                  </li>
                  <li>
                    <span>SEO</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <SEOSVG />
                    <h2 style={style.mainTitle}>
                      Seja uma das primeiras páginas acessadas
                    </h2>
                    <p style={style.subTitle}>
                      Seja uma das primeiras páginas acessadas
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é SEO?</h3>
                        <p style={style.p}>
                          A sigla SEO vem de Search Engine Optimization, que
                          podemos traduzir como Otimização para Buscadores. Os
                          serviços de SEO são utilizados para ranquear melhor as
                          páginas do seu site, segundo os critérios de qualidade
                          do Google, com o objetivo de coloca-lo nas primeiras
                          posições do tráfego orgânico nos resultados de
                          pesquisas. Ou seja, quando uma pessoa procura por um
                          produto/serviço que sua empresa oferece, a página do
                          seu site deve estar otimizada para que seja facilmente
                          encontrada pelo potencial cliente.
                        </p>
                        <h3 style={style.h3}>
                          Como o Google faz o ranqueamento?
                        </h3>
                        <p style={style.p}>
                          Os Google Bots examinam todas as páginas existentes no
                          imenso arquivo indexado do Google e, para cada uma
                          delas o Bot faz um checklist qualitativo com mais de
                          200 perguntas. Estas perguntas são bem específicas,
                          abrangendo aspectos técnicos de programação e
                          desenvolvimento, questões de{" "}
                          <HotLink to="/design/ux">usabilidade (UX)</HotLink> e
                          claro, a relevância e qualidade do conteúdo escrito na
                          página.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          A partir desse checklist, o Google ranqueia as páginas
                          de acordo com as notas aferidas, gerando assim a
                          página dos resultados orgânicos (Search Engine Results
                          Page ou SERP) para cada termo de pesquisa. O mais
                          impressionante é que todo esse processo é realizado
                          pelos robôs em um tempo médio de 1/8 de segundo.
                        </p>
                        <h3 style={style.h3}>Você sabia?</h3>
                        <p style={style.p}>
                          90% das pessoas que fazem pesquisas no Google clicam
                          apenas em resultados que aparecem na primeira página.
                          Cerca de 70% dos cliques para os 3 primeiros
                          resultados orgânicos
                        </p>

                        <p style={style.p}>
                          Aqui na dígitozero temos uma equipe capacitada em SEO,
                          capaz de aplicar métodos para fazer o seu site ser
                          melhor avaliado pelo Google. Solicite o contato de um
                          especialista e saiba como começar a melhorar o
                          ranqueamento do seu site.
                        </p>

                        <p style={{...style.p, margin:0}}>Oferecemos:</p>
                        <ul style={style.p}>
                          <li>Consultoria de SEO</li>
                          <li>Auditoria de SEO</li>
                          <li>Migração de SEO</li>
                          <li>Remoção de Penalização do Google</li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.SEO} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



