import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import AppSVG from "./AppSVG";

export default class App extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside aplicativo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>App</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/development">Desenvolvimento</Link>
                  </li>
                  <li>
                    <span>App</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <AppSVG />
                    <h2 style={style.mainTitle}>
                      Todas as plataformas e tipos de dispositivos
                    </h2>
                    <p style={style.subTitle}>
                      Faça um app para sua marca e fidelize seus clientes
                    </p>
                    <span style={style.hr} />
                    <p style={{ ...style.p, textAlign: "center" }}>
                      Se você tem um negócio, seja ele pequeno ou médio, mas
                      ainda não tenha certeza das razões para se ter um
                      aplicativo, veja estas seis razões que a equipe da{" "}
                      <HotLink to="/">dígitozero</HotLink> considera
                      importantíssimas:
                    </p>

                    <p style={{ ...style.p, textAlign: "center" }}>
                      Esteja na tela do celular do seu consumidor a todo momento<br />{" "}
                      Tenha um canal de marketing direto com seu público, envie
                      notificações com novidades e promoções.<br /> Forneça
                      valor aos clientes fiéis que baixaram seu app, dê a eles
                      uma experiência diferenciada e funcionalidades exclusivas<br />
                      Se diferencia da concorrência: quantos concorrentes seus
                      tem um aplicativo? Mostre que sua empresa é moderna!<br />
                      Construa uma imagem mais humanizada para sua marca e ganhe
                      reconhecimento<br /> Procure cultivar a lealdade do
                      cliente, manter clientes recorrentes é essencial para
                      qualquer negócio!
                    </p>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.UX} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



