import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import PageComponent from "@Components/core/PageComponent";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import style from "../styles.css";
import servicesName from "../services.constant";
import LinkedinAdsSVG from "./LinkedinAdsSVG";

export default class LinkedinAds extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside linkedin-ads">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Linkedin Ads</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/ads">Ads</Link>
                  </li>
                  <li>
                    <span>Linkedin Ads</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <LinkedinAdsSVG />
                    <h2 style={style.mainTitle}>
                      Anuncie para os profissionais
                    </h2>
                    <p style={style.subTitle}>
                      Para empresas que vendem produtos ou serviços ao mercado
                      corporativo
                    </p>
                    <span style={style.hr} />
                    <p style={{ ...style.p, textAlign: "center" }}>
                      O LinkedIn é uma rede social para uso exclusivamente
                      profissional. Para empresas que vendem produtos ou
                      serviços ao mercado corporativo, O LinkedIn trata-se de um
                      importante canal de marketing para encontrar novas
                      oportunidades e clientes. <br />
                      <br />Utilize o LinkedIn de maneira estratégica, anuncie
                      suas novidades, seus principais produtos e serviços.{" "}
                      <br />
                      <br />Divulgue conteúdo de qualidade de forma segmentada,
                      apenas para quem tem interesses relacionados.
                    </p>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.LINKEDIN_ADS} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



