import React, { Fragment } from "react";
import Footer from "@Components/layouts/common/Footer/Footer";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import AdsSVG from "./AdsSVG";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";

export default class Ads extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside ads">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <div id="banner-title">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <h1>
                          <strong>Ads</strong>
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Ads</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <AdsSVG />
                    <h2 style={style.mainTitle}>Publique</h2>
                    <p style={style.subTitle}>
                      Invista em propagandas online! Gere resultados expressivos
                      em menos tempo
                    </p>
                    <span style={style.hr} />
                    <p style={{ ...style.p, textAlign: "center" }}>
                      Invista em propagandas online e Gere resultados
                      expressivos em menos tempo<br /> Compre palavras-chave no{" "}
                      <HotLink to="/ads/google-adwords">Google Adwords</HotLink>.<br />{" "}
                      Sua empresa acima das concorrentes quando seu consumidor
                      pesquisar por produtos ou informações.<br /> Patrocine
                      posts nas redes sociais para que a pessoa que ainda não te
                      segue - mas faz parte do seu público-alvo – veja a sua
                      postagem e crie interesse.
                    </p>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.ADS} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



