import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import BlogCorporativoSVG from "./BlogCorporativoSVG";

export default class BlogCorporativo extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside blog-corporativo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Blog Corporativo</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/inbound-marketing">Inbound</Link>
                  </li>
                  <li>
                    <span>Blog Corporativo</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <BlogCorporativoSVG />
                    <h2 style={style.mainTitle}>
                      Conteúdo rico e relevante é fundamental
                    </h2>
                    <p style={style.subTitle}>
                      A estratégia independentemente de seu tamanho, setor ou
                      capacidade de investimento
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>
                          A importância do Blog Corporativo
                        </h3>
                        <p style={style.p}>
                          Atualmente, ter um blog corporativo com conteúdo rico
                          e relevante é fundamental para a estratégia de
                          Marketing Digital de qualquer empresa,
                          independentemente de seu tamanho, setor ou capacidade
                          de investimento. O blog corporativo é talvez o ponto
                          central e mais importante da estratégia digital, pois
                          tem impacto em várias frentes e, se bem conduzido,
                          pode trazer resultados extremamente positivos para o
                          seu negócio no médio e longo prazo.
                        </p>

                        <p style={style.p}>
                          Em primeiro lugar, adequar o blog corporativo da sua
                          empresa às melhores práticas de{" "}
                          <HotLink to="/inbound-marketing/seo">SEO</HotLink>{" "}
                          significa alavancar seu ranqueamento no Google e
                          garantir muito mais visibilidade para sua marca em
                          comparação aos concorrentes, resultando em novos leads
                          e oportunidades. Os algoritmos do buscador estão em
                          constante evolução, cada vez mais inteligentes e
                          capazes de avaliar a qualidade do seu conteúdo e o
                          quanto ele resolve as dores do seu público-alvo, ou
                          seja, avaliar se quem entra no seu blog encontra as
                          informações que procura.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          Quando se trata do conteúdo propriamente dito, o
                          objetivo do blog corporativo é ajudar sua empresa a
                          criar autoridade e credibilidade junto ao seu
                          público-alvo, pois dessa forma você demonstra ter
                          completo domínio sobre o assunto e se posiciona como
                          uma referência no mercado - algo que cria valor para
                          sua marca nos olhos dos consumidores. Agregar valor ao
                          potencial cliente é um fator que tende a pesar a seu
                          favor no momento da decisão de compra, portanto
                          deve-se investir em um editorial de conteúdo de alta
                          qualidade para o blog corporativo do seu negócio. É
                          importante também que os materiais tenham consistência
                          para atender às expectativas de quem assiste sua
                          empresa e anseia pelo conteúdo de alto nível.
                        </p>

                        <p style={style.p}>
                          Aqui na dígitozero desenvolvemos blogs personalizados
                          para empresas de qualquer ramo e tamanho, pois sua
                          estratégia de <HotLink to="/inbound-marketing">inbound marketing</HotLink> merece um blog
                          corporativo de respeito!
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.CORPORATE_BLOG} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



