import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import DevelopmentSVG from "./DevelopmentSVG";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";

export default class Development extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside development">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Desenvolvimento</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Desenvolvimento</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <DevelopmentSVG />
                    <h2 style={style.mainTitle}>Muito mais que uma logo</h2>
                    <p style={style.subTitle}>Sites Otimizados para SEO, com design impecável e ótima usabilidade</p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          A dígitozero conta com um time de desenvolvedores
                          experiente e treinado nas principais tecnologias que o
                          mercado oferece. Somos especialistas em
                          desenvolvimento web, criamos sites, aplicativos e
                          lojas online em diversas linguagens e para diversas
                          plataformas.
                        </p>
                        <h3 style={style.h3}>Websites</h3>
                        <p style={style.p}>
                          Sites Otimizados para{" "}
                          <HotLink to="/inbound-marketing/seo">SEO</HotLink> com
                          design impecável e ótima <HotLink to="/design/ux">
                            usabilidade
                          </HotLink>.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Apps</h3>
                        <p style={style.p}>Faça seu aplicativo com a gente, fidelize seu cliente através de uma boa experiência!</p>
                        <h3 style={style.h3}>E-Commerce</h3>
                        <p style={style.p}>Sua loja virtual pensada estrategicamente. Ofereça a melhor experiência de compra para o seu cliente e ele voltará!</p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.DEVELOPMENT} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



