const style = {
  breadcrumb: {
    backgroundColor: "#F2F2F2"
  },
  background: {
    backgroundColor: "#F2F2F2",
    paddingBottom: "3.688rem"
  },
  subContainer: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  mainTitle: {
    textAlign: "center",
    color: "#313131",
    fontSize: "1.875rem",
    fontWeight: "800",
    textTransform: "uppercase",
    fontFamily: "Raleway, sans-serif"
  },
  subTitle: {
    textAlign: "center",
    fontSize: "1.125rem",
    fontWeight: "500"
  },
  p: {
    textAlign: "justify",
    fontWeight: "500",
    marginBottom: "2rem"
  },
  hr: {
    borderBottom: "solid 3px #12C2E9",
    width: "2.688rem",
    marginBottom: "2.46875rem"
  },
  h3: {
    fontSize: "1rem",
    textAlign: "left",
    fontFamily: "Raleway, sans-serif",
    fontWeight: 800
  }
};

export default style;


