import React, { Fragment } from "react";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "@Components/core/PageComponent";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import style from "../styles.css";
import servicesName from "../services.constant";
import GoogleAdwordsSVG from "./GoogleAdwordsSVG";

export default class GoogleAdwords extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside google-adwords">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Google Adwords</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/ads">Ads</Link>
                  </li>
                  <li>
                    <span>Google Adwords</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <GoogleAdwordsSVG />
                    <h2 style={style.mainTitle}>
                      O serviço de publicidade online
                    </h2>
                    <p style={style.subTitle}>
                      Consultoria de Links Patrocinados
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é o Google Adwords</h3>
                        <p style={style.p}>
                          Google Adwords é a plataforma através da qual são
                          feitos anúncios pagos no Google, chamados de links
                          patrocinados. Eles são a melhor forma de conseguir
                          exposição rápida e atrair cliques para seu site, pois
                          te colocam no topo das páginas de resultados de
                          pesquisa em posição de destaque. Os links patrocinados
                          também são comumente usados para anunciar os
                          principais produtos das empresas, além de promoções e
                          outros tipos de campanha de tempo limitado.
                        </p>
                        <h3 style={style.h3}>
                          Como o Google Adwords funciona?
                        </h3>
                        <p style={style.p}>
                          O Google Adwords é o principal serviço de publicidade
                          online do Google e responsável por grande parte da sua
                          receita. Os serviços do Adwords são populares entre
                          empresas do mundo todo e largamente utilizados por
                          aquelas que tem uma estratégia de Inbound Marketing
                          integrada, pois o Google Adwords é uma das formas mais
                          rápidas e eficazes de atrair tráfego para o seu site,
                          app ou blog. Qualquer pessoa ou empresa pode utilizar
                          o Google Ads, basta criar uma conta no site e
                          cadastrar um meio de pagamento, onde o cartão de
                          crédito é o meio mais comumente escolhido.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>
                          Os serviços são cobrados através de 2 métricas
                          distintas: CPM ou CPC.
                        </h3>
                        <p style={style.p}>
                          CPM é o Custo por Mil Impressões, ou seja, você é
                          cobrado a cada mil visualizações do seu anúncio. É a
                          métrica mais indicada se o objetivo de Marketing for
                          divulgar um produto ou serviço, para que se tenha
                          ideia de quantas pessoas a propaganda impactou. CPC é
                          o Custo Por Clique, ou seja, o valor é cobrado apenas
                          se a pessoa clicar no seu anúncio. É uma métrica
                          bastante utilizada por empresas que querem aumentar o
                          número de visitantes no site e adquirir mais leads que
                          resultem em novas oportunidades de negócio.
                        </p>
                        <h3 style={style.h3}>
                          Como a dígitozero pode te ajudar?
                        </h3>
                        <p style={style.p}>
                          Fazemos uma Consultoria de Links Patrocinados em que
                          conduzimos o cliente desde o Planejamento da Campanha
                          até a mensuração e otimização de resultados. Cada
                          cliente tem um caso diferente, então aqui na
                          dígitozero personalizamos cada projeto com as
                          especificidades necessárias.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.GOOGLE_ADWORDS} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



