import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import BrandingSVG from "./BrandingSVG";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";

export default class Branding extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside branding">
            <NavBar />
            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Branding</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>Branding</span>
                  </li>
                </Breadcrumb>
                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <BrandingSVG />
                    <h2 style={style.mainTitle}>Muito mais que uma logo</h2>
                    <p style={style.subTitle}>
                      Pode ser traduzido de maneira geral como Gestão de Marca
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é Branding?</h3>
                        <p style={style.p}>
                          Pode ser considerado como atividade de Branding,
                          qualquer atividade realizada com o objetivo de tornar
                          sua marca mais conhecida, reconhecida, desejada e
                          positiva, tanto no pensamento quanto no coração do seu
                          público-alvo. A Gestão de Marca abrange desde a
                          concepção de uma logomarca e identidade visual, até as
                          atividades mais simples de marketing do cotidiano de
                          uma empresa, como um post no Instagram, por exemplo.
                          Portanto, o branding está presente em todas as partes
                          do Marketing de Conteúdo e, da mesma forma, permeia
                          toda a estratégia de Inbound Marketing.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>A importância do Branding</h3>
                        <p style={style.p}>
                          A importância de uma boa gestão de marca está na
                          resposta da seguinte pergunta:
                        </p>
                        <p style={style.p}>
                          “Porque você gosta mais de uma marca em detrimento de
                          outra, mesmo sabendo que as duas oferecem um produto
                          ou serviço bastante similar e que resolve seu problema
                          da mesma forma?”
                        </p>
                        <p style={style.p}>
                          O cérebro humano é bastante complexo e abrange fatores
                          racionais e irracionais, por isso é importante ser
                          mais do que apenas uma empresa que vende algo, é
                          preciso vender uma ideia.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.BRANDING} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



