import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import servicesName from "../services.constant";
import LojaVirtualSVG from "./LojaVirtualSVG";

export default class LojaVirtual extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside ecommerce">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Loja Virtual</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/development">Desenvolvimento</Link>
                  </li>
                  <li>
                    <span>Loja Virtual</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <LojaVirtualSVG />
                    <h2 style={style.mainTitle}>Mais que apenas sites</h2>
                    <p style={style.subTitle}>Crie seu e-commerce aqui!</p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Crie sua Loja Virtual aqui!</h3>
                        <p style={style.p}>
                          Uma loja virtual bem-sucedida deve buscar sempre a
                          melhor experiência possível de compra para o cliente.
                          Não importa o setor ou ramo de atuação, valor do
                          produto, tipo de entrega, o mais importante é que o
                          e-commerce conduza o consumidor de forma objetiva,
                          fácil e atraente até a compra e entrega do produto,
                          respeitando padrões universais de usabilidade e
                          processo de compras ágil. É fundamental ter foco nas
                          necessidades e anseios do consumidor, para que ele se
                          surpreenda positivamente com a conveniência da compra
                          e se torne um cliente recorrente e fidelizado à marca.
                        </p>
                        <p style={style.p}>
                          O mercado de varejo online vem crescendo ano a ano e é
                          cada vez mais competitivo e popularizado, por isso é
                          necessário investir para se destacar da concorrência.
                          Os hábitos de consumo mudaram e seu negócio precisa
                          estar adaptado a esta realidade.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>10 DICAS para o sucesso de uma loja virtual segundo a dígitozero:</h3>
                        <ol style={{...style.p, listStylePosition:"inside", padding:0}}>
                            <li>Facilidade é a palavra! Facilite a vida do cliente e encurte ao máximo o processo de compra</li>
                            <li>Utilize ícones que facilitem a compreensão do cliente sem necessidade de ler instruções</li>
                            <li>Apresente feedback constante às interações dos usuários, mantenha-o informado sobre o passo-a-passo</li>
                            <li>Dê <HotLink to="/development/app">mobilidade</HotLink> ao seu e-commerce e garanta que esteja adaptado para todos os dispositivos, celulares, tablets e desktops</li>
                            <li>Invista na imagem do site, capriche na qualidade das fotos dos produtos e tente sempre humanizar a página</li>
                            <li>Busque sempre agregar valor ao cliente, sendo mais ágil que o esperado e sem problemas na compra e na entrega do produto</li>
                            <li>Certifique-se que as páginas estão carregando rápido e funcionando corretamente</li>
                            <li>Invista em uma logística adequada e estratégica, que diminua o custo final para o cliente</li>
                            <li>Invista em <HotLink to="/inbound-marketing/seo">SEO</HotLink> para otimizar a busca pelo seu site no Google; Seja achado com facilidade pelo seu público-alvo em pesquisas</li>
                            <li>Busque sempre encantar o cliente com uma experiência melhor do que ele esperaria! Isso te garantirá novos clientes</li>
                            
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.ECOMMERCE} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



