import React, { Fragment } from "react";
import Footer from "@Components/layouts/common/Footer/Footer";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import style from "../styles.css";
import FacebookAdsSVG from "./FacebookAdsSVG";

export default class FacebookAds extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside facebook-ads">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <div id="banner-title">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <h1>
                          <strong>Facebook Ads</strong>
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/ads">Ads</Link>
                  </li>
                  <li>
                    <span>Facebook Ads</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <FacebookAdsSVG />
                    <h2 style={style.mainTitle}>
                      O serviço de publicidade online
                    </h2>
                    <p style={style.subTitle}>
                      Use a maior rede social do mundo
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que é o Facebook Ads?</h3>
                        <p style={style.p}>
                          O Facebook Ads é a ferramenta que exibe os anúncios
                          dentro do Facebook. Nele é possível patrocinar seus
                          posts para que atinjam uma parcela de pessoas que não
                          seguem sua empresa na rede social, mas que tem
                          características semelhantes à sua persona de cliente,
                          ou seja, é possível segmentar cada anúncio para
                          aumentar sua assertividade.
                        </p>

                        <p style={style.p}>
                          O Facebook é a maior rede social do mundo, com cerca
                          de 2 bilhões de perfis ativos, o que a torna um
                          terreno fértil para propagação de ideias e para
                          divulgação de produtos e serviços. Atualmente quase 3
                          milhões de empresas pagam para anunciar conteúdos
                          patrocinados na plataforma do Facebook Ads.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          A ferramenta é muito útil devido à quantidade de
                          informações que o Facebook tem de seus usuários, como
                          sexo, idade, localização, interesses e preferências.
                          Com isso, é possível segmentar bastante os anúncios
                          patrocinados e fazer com que estes sejam visualizados
                          por indivíduos que tenham, de fato, chance de se
                          interessar pelo que a sua empresa faz.
                        </p>

                        <p style={style.p}>
                          O Facebook disponibiliza algumas formas e locais
                          possíveis de anunciar, é bom que se tenha uma
                          estratégia para impactar quem você deseja da melhor
                          forma possível.
                        </p>

                        <p style={style.p}>
                          Fale com um especialista, comece a patrocinar posts no
                          Facebook e aumente seus resultados agora!
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.FACEBOOK_ADS} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



